import React, {useState} from 'react';
import './App.css';
import marked from 'marked';
import DOMpurify from 'dompurify';

function App() {
  const [markDown, setMarkDown] = useState(
    '# A Header\n\n## A sub header\n\n[Here\'s a link to FreeCodeCamp](https://www.FreeCodeCamp.org)\n\n```I think this may be inline code?```\n\n    this is a codeblock\n\n1. this\n2. is\n3. a\n4. list\n\n>this is a blockquote\n>\n>>and this is nested\n\n![this is react\'s logo](logo512.png "React logo")\n\nthis is **bold** text');
  marked.setOptions({breaks: true})
  return (
    <div className="App">
    <textarea
      id="editor"
      value = {markDown}
      onChange = {(event) => {setMarkDown(event.target.value)}}/>
    <div id="preview" dangerouslySetInnerHTML= {{__html: DOMpurify.sanitize(marked(markDown))}}/>
    </div>
  );
}


export default App;
